/* Header */
.header {
  min-height: 450px;
  height: 70vh;
  background: #8a8a8a; }
  .header .main-menu {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    min-height: 4em;
    position: relative;
    align-self: flex-start; }
    .header .main-menu .bar {
      position: absolute;
      top: 0;
      right: 0;
      color: #fff;
      padding: 1em;
      text-align: right;
      cursor: pointer;
      display: none;
      z-index: 90;
      text-transform: uppercase;
      text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.2); }
      .header .main-menu .bar .text {
        display: inline-block;
        font-size: 1em;
        margin-top: 0.35em;
        margin-right: 0.4em;
        vertical-align: top; }
      .header .main-menu .bar .icon {
        display: inline-block;
        width: 30px;
        height: 30px; }
      .header .main-menu .bar .cancel {
        display: none; }
      @media (max-width: 46.24em) {
        .header .main-menu .bar {
          display: block; } }
    .header .main-menu .logo {
      position: absolute;
      font-size: 1.3em;
      color: #fff;
      display: block;
      text-transform: uppercase;
      top: 0;
      left: 0;
      padding: 0 .9em;
      z-index: 90;
      text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.2); }
      .header .main-menu .logo img,
      .header .main-menu .logo h1 {
        vertical-align: top;
        display: inline-block; }
      .header .main-menu .logo img {
        margin-top: .5em;
        height: 2em; }
      .header .main-menu .logo h1 {
        margin: 1em 0 0 .3em; }
    .header .main-menu ul {
      display: block;
      text-align: right; }
      .header .main-menu ul li {
        display: inline-block;
        padding: 1.5em; }
        .header .main-menu ul li.current_page_item a {
          border-bottom: 1px solid #cccccc; }
        .header .main-menu ul li a {
          display: block;
          text-transform: uppercase;
          color: #fff;
          text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.2);
          padding-bottom: 5px;
          border-bottom: 1px solid transparent; }
          .header .main-menu ul li a:hover {
            color: #cccccc;
            border-bottom: 1px solid #cccccc; }
          @media (max-width: 46.24em) {
            .header .main-menu ul li a {
              padding: 1em 2em; }
              .header .main-menu ul li a:hover {
                border-bottom: 0; } }
        @media (max-width: 46.24em) {
          .header .main-menu ul li {
            display: block;
            text-align: left;
            padding: 0; }
            .header .main-menu ul li:hover {
              background: rgba(26, 26, 26, 0.8); } }
      @media (max-width: 46.24em) {
        .header .main-menu ul {
          display: none; } }
    @media (max-width: 46.24em) {
      .header .main-menu ul {
        transition: opacity 0.2s ease-out;
        opacity: 0; }
      .header .main-menu.open ul {
        display: block;
        background: rgba(0, 0, 0, 0.9);
        padding-top: 4em;
        padding-bottom: 1em;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        opacity: 1;
        z-index: 80; } }

